.home {
  width: 100%;
  align-items: center;
  font-family: "Arial", sans-serif;
  color: #3e497a;
}

.about {
  width: 100%;
  height: 800px; /* Changed height to auto */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  background-color: #3e497a;
  color: rgb(234, 232, 110);
}

.about h2 {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 80px;
  color: rgb(234, 232, 110);
  height: 50px;
}

.about .prompt {
  width: 80%; /* Changed width to 80% */
  font-size: 30px;
}

.prompt svg {
  font-size: 60px;
  margin: 5px;
  color: white;
}

.skills {
  font-size: 35px;
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.skills h1 {
  color: #3e497a;
}

.skill-container {
  display: flex;
  justify-content: space-around; /* Changed from center to space-around */
  width: 80%; /* Adjusted width */
}

.skill-category {
  text-align: center;
}

.skill-category h2 {
  color: #3e497a;
  font-size: 24px;
  margin-bottom: 10px;
}

.skill-category ul {
  list-style: none;
  padding: 0;
}

.skill-category ul li {
  font-size: 20px;
}

@media only screen and (max-width: 600px) {
  .about h2 {
    font-size: 40px;
  }

  .about .prompt {
    margin-top: 10px;
    font-size: 20px;
    width: 90%; /* Adjusted width */
  }

  .skills {
    text-align: center;
  }

  .skill-container {
    flex-direction: column; /* Changed to column for smaller screens */
    align-items: center; /* Center items in smaller screens */
    width: 90%; /* Adjusted width */
  }

  .skill-category h2 {
    font-size: 30px;
  }
}
